<template>
    <span>
        <span v-for="(type, itype) in types">

            <span v-if="type.isPrimitive" :class="type.cssClass">{{ type.name }}</span>

            <!--'#!/' + type.type-->
            <a v-if="type.isEntity && showTooltip === false"
               href="javascript:;"
               @click.stop="onEntityClick(type)"
               :target="target"
               :class="type.cssClass"
               :href="href"
            >{{ fullName ? type.fullName : type.name }}</a>

            <a ref="popoverLink"
               v-if="type.isEntity && showTooltip === true"
               href="javascript:;"
               :class="type.cssClass"
               @click.stop="onEntityClick(type)"
               :target="target"
               :href="href"
            >{{ fullName ? type.fullName : type.name }}</a>

            <ui-popover
                    trigger='popoverLink'
                    open-on="hover"
                    dropdown-position="bottom left"
                    v-if="type.isEntity && showTooltip === true"
                    class='popover-phpdoc'>
                <span :class="type.cssClass">
                    {{ type.fullName }}
                </span>
            </ui-popover>

            <span v-if="seperator.length > 0" v-show="itype < types.length -1" class="type-seperator">{{ seperator  }} </span>
        </span>
    </span>
</template>

<style lang="scss" rel="stylesheet/scss">
    @import '../../core/styles/base';

    .popover-phpdoc {
        padding: 1px 10px;
    }
</style>

<script>
    import {mapGetters, mapActions} from 'vuex';
    import {removeStartSlash} from '../utils'
    import {Type} from '../'

    import {find, last, isArray, isString} from 'lodash' //, isNumber, isObject, isUndefined, isBoolean, isNull

    import UiPopover from '../../core/components/UiPopover.vue'

    export default {
        name    : 'pd-type',
        components: { UiPopover },
        props   : {
            type             : {type: [String, Array], default: []},
            fullName         : Boolean,
            noTooltip        : Boolean,
            showIcon         : Boolean,
            showIconInTooltip: Boolean,
            target           : {type: String, default: '_self'},
            href             : {type: String, default: 'javascript:;'},
            tooltip          : {
                type: Object, default(){
                    return {effect: 'scale', placement: 'top'}
                }
            },
            seperator        : {type: String, default: '|'},
            static           : String
        },

        data(){
            return {
                types     : [],
                isInternal: false
            }
        },
        mounted(){
            if ( this.type ) this.types = this.parseType(this.type);
        },
        watch   : {
            type(newVal){
                this.types = this.parseType(newVal);
            },
            entities(){
                this.types = this.parseType(this.type);
            }
        },
        methods : {
            ...mapActions(['updatePhpdoc']),
            parseType(type){
                if ( isString(type) ) {
                    type = type.split('|');
                }
                if ( isArray(type) ) {
                    type = type.map(t => new Type(t.toString(), this));
                }
//
                return type;
            },
            onEntityClick(type){
                this.$emit('click', type);
                this.$events.$emit('phpdoc:type:click', type);
            }
        },
        computed: {
            ...mapGetters(['entities']),
            showTooltip(){
                return this.noTooltip === false;
            }
        }
    }
</script>


