<template>
    <c-side-nav ref="sidenav" :is-opened="show" :right="true" :width="400" slot="after" @after-leave="hide">
        <form class="phpdoc-settings-form">
            <h4>PHPDoc settings</h4>
            <ui-tabs fullwidth>

                <ui-tab title="General">
                    My collections
                </ui-tab>

                <ui-tab title="Methods">

                    <div class="form-group">
                        <div class="pretty success smooth"><input type="checkbox" v-model="inherited"> <label><i class="fa fa-check"></i> Show inherited {{ type }}</label></div>
                    </div>
                    <div class="form-group">
                        <div class="pretty success smooth"><input type="checkbox" v-model="public"> <label><i class="fa fa-check"></i> Show public {{ type }}</label></div>
                    </div>
                    <div class="form-group">
                        <div class="pretty success smooth"><input type="checkbox" v-model="protected"> <label><i class="fa fa-check"></i> Show protected {{ type }}</label></div>
                    </div>
                    <div class="form-group">
                        <div class="pretty success smooth"><input type="checkbox" v-model="private"> <label><i class="fa fa-check"></i> Show private {{ type }}</label></div>
                    </div>

                    <label for="sort-by" class="dropdown-header">Sorting</label>
                    <div class="form-group">
                        <select class="form-control" id="sort-by" v-model="sortBy">
                            <option v-for="option in sortMethodsOptions">{{ option }}</option>
                        </select>
                    </div>

                    <div class="form-group">
                        <label> <input type="radio" value="asc" v-model="sortDir"> Ascending </label>
                    </div>
                    <div class="form-group">
                        <label> <input type="radio" value="desc" v-model="sortDir"> Descending </label>
                    </div>

                </ui-tab>

                <ui-tab title="Properties">
                    Authors
                </ui-tab>

            </ui-tabs>


            <div class="form-group">
                <a href="#" class="btn btn-secondary btn-block" @click.prevent="resetSettings()">Reset to default</a>
            </div>
            <div class="form-group">
                <a href="#" class="btn btn-primary btn-block" @click.prevent="closeMenu()">Close</a>
            </div>
        </form>
    </c-side-nav>
</template>

<style lang="scss" rel="stylesheet/scss" scoped>
    @import '../../core/styles/base';

    .phpdoc-settings-form {
        padding: 20px 20px;
        .form-group {
            margin-bottom: 2px;
        }
        a.btn {
            margin: 0
        }
    }
</style>

<script>
    import {mapGetters, mapActions} from 'vuex';
    import * as transitions from 'codex-core/transitions'
console.log({transitions});

    export default {
        type      : 'pd-settings',
        props     : {},
        data(){
            return {
                sortMethodsOptions: ['name', 'visibility'],
                type: 'methods',
                show: false
            }
        },
        methods   : {
            ...transitions.slide,
            ...mapActions(['updatePhpdocSettings', 'resetPhpdocSettings']),
            show(){
                this.$refs.sidenav.show();
            },
            hide(){
                this.$refs.sidenav.close();
            },
            toggle(){
                this.$refs.sidenav.toggle()
            },

            resetSettings(){
                this.resetPhpdocSettings(this.type)
            }
        },
        computed  : {
            settings () {
                return this.$store.getters.settings[this.type];
            },
            inherited: {
                get(){
                    return this.settings.show.inherited;
                },
                set(v){
                    this.updatePhpdocSettings({type: this.type, settings: {show: {inherited: v}}});
//                    this.settings.show.inherited = v;
                },
            },
            public   : {
                get(){
                    return this.settings.show.public;
                },
                set(v){
                    this.updatePhpdocSettings({type: this.type, settings: {show: {public: v}}});
                },
            },
            protected: {
                get(){
                    return this.settings ? this.settings.show.protected : false;
                },
                set(v){
                    this.updatePhpdocSettings({type: this.type, settings: {show: {protected: v}}});
                },
            },
            private  : {
                get(){
                    return this.settings ? this.settings.show.private : false;
                },
                set(v){
                    this.updatePhpdocSettings({type: this.type, settings: {show: {private: v}}});
                },
            },
            sortBy   : {
                get(){
                    return this.settings ? this.settings.sort.by : '';
                },
                set(v){
                    this.updatePhpdocSettings({type: this.type, settings: {sort: {by: v}}});
                },
            },
            sortDir  : {
                get(){
                    return this.settings ? this.settings.sort.dir : 'asc'
                },
                set(v){
                    this.updatePhpdocSettings({type: this.type, settings: {sort: {dir: v}}});
                },

            }
        }
    }
</script>


