<template>
    <div>

        <div @contextmenu.prevent="$refs.ctxMenu.open">
            <slot></slot>
        </div>

        <c-context-menu id="context-menu" ref="ctxMenu">
            <ui-menu slot="menu"
                     contain-focus
                     has-icons
                     has-secondary-text
                     disable-ripple

                     :options="menuOptions"
                     @close="$refs.ctxMenu.close()"
            >
                <template scope="props" slot="option">
                    <div class="ui-menu-option__content">
                        <ui-icon v-if="props.option.icon"
                                 class="ui-menu-option__icon"
                                 :icon-set="props.option.iconProps.iconSet"
                                 :icon="props.option.icon"
                                 :remove-text="props.option.iconProps.removeText"
                                 :use-svg="props.option.iconProps.useSvg"

                        ></ui-icon>
                        <ui-checkbox
                                v-if="props.option.type==='checkbox'"
                                class="ui-menu-option__checkbox"
                                v-model="props.option.model"
                        >

                        </ui-checkbox>
                        <div class="ui-menu-option__text">{{ props.option.type }} = label</div>
                        <div class="ui-menu-option__secondary-text" v-if="props.option.secondaryText">
                            {{ props.option.secondaryText }}
                        </div>
                    </div>
                </template>
            </ui-menu>
        </c-context-menu>
        <!--
                <c-context-menu id="context-menu" ref="ctxMenu">
                    <form>
                        <h6 class="dropdown-header">Settings</h6>
                        <div class="dropdown-item pretty success smooth"><input type="checkbox" v-model="inherited"> <label><i class="fa fa-check"></i> Show inherited {{ name }}</label></div>
                        <div class="dropdown-item pretty success smooth"><input type="checkbox" v-model="public"> <label><i class="fa fa-check"></i> Show public {{ name }}</label></div>
                        <div class="dropdown-item pretty success smooth"><input type="checkbox" v-model="protected"> <label><i class="fa fa-check"></i> Show protected {{ name }}</label></div>
                        <div class="dropdown-item pretty success smooth"><input type="checkbox" v-model="private"> <label><i class="fa fa-check"></i> Show private {{ name }}</label></div>
                        <label for="sort-by" class="dropdown-header">Settings</label>
                        <div class="dropdown-item">
                            <select class="form-control" id="sort-by" v-model="sortBy">
                                <option v-for="option in sortMethodsOptions">{{ option }}</option>
                            </select>
                        </div>
                        <div class="dropdown-item">
                            <label> <input type="radio" value="asc" v-model="sortDir"> Ascending </label>
                        </div>
                        <div class="dropdown-item">
                            <label> <input type="radio" value="desc" v-model="sortDir"> Descending </label>
                        </div>
                        <div class="dropdown-item">

                            <a href="#" class="btn btn-secondary btn-block" @click.prevent="resetSettings()">Reset to default</a>
                        </div>
                        <div class="dropdown-item">
                            <a href="#" class="btn btn-primary btn-block" @click.prevent="closeMenu()">Close</a>
                        </div>
                    </form>
                </c-context-menu>
                -->
    </div>

</template>

<style lang="scss" rel="stylesheet/scss">
    @import '../../core/styles/base';

</style>

<script>
    import {mapGetters, mapActions} from 'vuex';


    const iconProps = {
        iconSet   : 'fa',
        removeText: true,
        useSvg    : false
    }
    function setOptionProperties(option, props) {
        Object.keys(props).forEach(key => {
            option[key] = props[key];
        });
        option['disableRipple'] = true;
        return option;
    }

    export default {
        name      : 'pd-list-settings-menu',
        props     : {
            type: String
        },
        data(){
            return {
                sortMethodsOptions: ['name', 'visibility'],
                menuOptions       : [
                    {
                        id   : 'inherited',
                        label: 'Show inherited',
                        type : 'checkbox',
                        model: this.inherited
                    },
                    {
                        id   : 'protected',
                        label: 'Show protected',
                        type : 'checkbox',
                        model: this.protected
                    },
                    {
                        id           : 'share',
                        label        : 'Share',
                        icon         : 'fa-share',
                        secondaryText: 'Ctrl+Shift+S',
                        disabled     : true
                    },
                    {
                        type: 'divider'
                    },
                    {
                        id           : 'delete',
                        label        : 'Delete',
                        icon         : 'fa-delete',
                        secondaryText: 'Del'
                    }
                ].map(option => setOptionProperties(option, {iconProps}))
//                settings          : false
            }
        },
        mounted(){

        },
        methods   : {
            ...mapActions(['updatePhpdocSettings', 'resetPhpdocSettings']),
            closeMenu(){
                this.$refs.ctxMenu.close()
            },
            resetSettings(){
                this.resetPhpdocSettings(this.type)
            }
        },
        computed  : {
            settings () {
                return this.$store.getters.settings[this.type];
            },
            inherited: {
                get(){
                    return this.settings.show.inherited;
                },
                set(v){
                    this.updatePhpdocSettings({type: this.type, settings: {show: {inherited: v}}});
//                    this.settings.show.inherited = v;
                },
            },
            public   : {
                get(){
                    return this.settings.show.public;
                },
                set(v){
                    this.updatePhpdocSettings({type: this.type, settings: {show: {public: v}}});
                },
            },
            protected: {
                get(){
                    return this.settings ? this.settings.show.protected : false;
                },
                set(v){
                    this.updatePhpdocSettings({type: this.type, settings: {show: {protected: v}}});
                },
            },
            private  : {
                get(){
                    return this.settings ? this.settings.show.private : false;
                },
                set(v){
                    this.updatePhpdocSettings({type: this.type, settings: {show: {private: v}}});
                },
            },
            sortBy   : {
                get(){
                    return this.settings ? this.settings.sort.by : '';
                },
                set(v){
                    this.updatePhpdocSettings({type: this.type, settings: {sort: {by: v}}});
                },
            },
            sortDir  : {
                get(){
                    return this.settings ? this.settings.sort.dir : 'asc'
                },
                set(v){
                    this.updatePhpdocSettings({type: this.type, settings: {sort: {dir: v}}});
                },

            }
        }
    }
</script>


