<template>
    <div class="phpdoc-app" :style="style">
        <pd-stylesheet></pd-stylesheet>
        <!--custom content-->
        <c-centered v-if="!file || loading">
            <c-loader></c-loader>
        </c-centered>
        <div class="row" v-if="file && !loading">
            <span class="phpdoc-settings-button" @click="$refs.settings.toggle()">settings</span>

            <pd-settings ref="settings"></pd-settings>
            <div class="col-md-12">
                <c-drag-zone class="zone">

                    <!--Tree-->
                    <div class="item" v-scrollable="height">
                        <pd-tree ref="tree"
                                 :query="entity.full_name"
                                 v-on:select="onTreeSelect"
                        ></pd-tree>
                    </div>

                    <!--Handle-->
                    <c-drag-zone-handle class="handle" v-on:change="saveZoneWidths" disabled></c-drag-zone-handle>

                    <!--Entity - Method List - Property List -->
                    <div class="item" v-scrollable="height">

                        <pd-entity ref="entity"
                                   :query="entity.full_name"
                        ></pd-entity>

                        <div class="tabbable phpdoc-tabs">
                            <ul role="tablist" class="nav nav-tabs phpdoc-tabs-nav">
                                <li role="presentation"
                                    class="nav-item"
                                    :class="{ 'active' : isActive('methods') }">
                                    <a href="javascript:;"
                                       class="nav-link"
                                       @click.prevent="setActive('methods')"
                                       aria-controls="phpdoc-methods"
                                       role="tab"
                                    >Methods</a>
                                </li>

                                <li role="presentation"
                                    v-if="file.type !== 'interface'"
                                    class="nav-item"
                                    :class="{ 'active' : isActive('properties') }">
                                    <a href="javascript:;"
                                       class="nav-link"
                                       @click.prevent="setActive('properties')"
                                       aria-controls="phpdoc-properties"
                                       role="tab"
                                    >Properties</a></li>

                                <li role="presentation"
                                    class="nav-item"
                                    :class="{ 'active' : isActive('source') }">
                                    <a href="javascript:;"
                                       class="nav-link"
                                       @click.prevent="setActive('source')"
                                       aria-controls="phpdoc-source"
                                       role="tab"
                                    >Source</a></li>
                            </ul>
                            <div class="tab-content" ref="tabContent">

                                <div role="tabpanel" class="tab-pane phpdoc-tab-methods" :class="{ 'active' : isActive('methods') }">
                                    <div class="tabbable tabs-left">
                                        <pd-list-settings-menu list="methodList" type="methods">

                                            <pd-method-list
                                                    ref="methodList"
                                                    :query="entity.full_name"
                                                    selectable
                                                    store-settings
                                                    v-on:select="onMethodSelect"
                                            ></pd-method-list>

                                            <!--use-signature-->
                                            <!--signature-hide-modifiers-->
                                            <!--signature-hide-argument-types-->
                                            <!--signature-hide-argument-defaults-->
                                            <!--signature-hide-argument-return-->
                                            <!--signature-hide-return-->
                                        </pd-list-settings-menu>
                                        <p class="text-sm-right pr-sm fs-10">Right-click the methods for more options</p>
                                    </div>
                                </div>

                                <div role="tabpanel" class="tab-pane phpdoc-tab-properties" :class="{ 'active' : isActive('properties') }">
                                    <div class="tabbable tabs-left">
                                        <pd-property-list
                                                ref="propertyList"
                                                :query="entity.full_name"
                                        ></pd-property-list>
                                    </div>
                                </div>


                                <div role="tabpanel" class="tab-pane phpdoc-tab-source">
                                    <!--:class="{ 'active' : isActive('source') }"-->

                                </div>
                            </div>
                        </div>
                    </div>

                    <!--Handle -->
                    <c-drag-zone-handle class="handle" v-on:change="saveZoneWidths"></c-drag-zone-handle>

                    <!--Method-->
                    <div class="item" v-scrollable="height">
                        <pd-method ref="method" v-if="content === 'method' && query" :query="query"></pd-method>
                        <pd-source
                                ref="source"
                                v-if="content === 'source'"
                                :query="entity.full_name"
                        >

                        </pd-source>
                    </div>
                </c-drag-zone>
            </div>
        </div>
    </div>
</template>


<script>
    import {mapGetters, mapActions} from 'vuex'
    import S from 'string'
    import {component} from '../mixins'
    import {resize} from 'codex-core/mixins'
    import Vue from 'vue'
    import HashPathParser from '../classes/HashPathParser'


    export default {
        name    : 'phpdoc',
        mixins  : [component, resize],
        props   : {
            fullName: String,
            height  : Number

        },
        data(){
            return {
                methodName : false,
                methodQuery: false,
                file       : false,
                loading    : true,
                query      : null,

                active           : 'methods',
                content          : 'method',
                selectedProperty : false,
                selectedMethod   : false,
                hasSlimScroll    : false,
                slimScrollTimeout: 0,
                staticZoneWidths : [280, null, null]
            }
        },
        beforeMount(){

            // this.navigator = new Navigator()
            let hashPath = HashPathParser.parse();
            if ( hashPath.isHashPath() ) {
                // do we have a hash path? if so, we set the query by that
                this.query = hashPath.toQuery();
                window.history.replaceState(null, hashPath.toString(), window.location.pathname + hashPath.toString());
            } else if ( Vue.codex.phpdoc.defaultClass ) {
                // we don't have a hash path? we check for default class for the project and set query by that, then update the hash path
                this.open(Vue.codex.phpdoc.defaultClass);
            }

            window.addEventListener("popstate", (event) => {
                let hashPath = HashPathParser.parse();
                console.log('popstate', window.location, event);
                if ( hashPath.isHashPath() ) {
                    this.open(hashPath.toQuery());
                }
            }, false);

        },
        mounted(){
            if ( this.query )
                this.open(this.query);
            else
                this.$phpdoc.getEntities().then((entities) => this.open(this.entities[0].full_name));


            this.$$ready(() => setTimeout(() => {
                this.loading = false
                this.$nextTick(() => this.loadZoneWidths());
            }, 500));


            this.$events.$on('phpdoc:type:click', (type) => {
                if ( type.isLocal ) this.open(type.fullName);
            });

        },
        computed: {
            entities(){
                return this.$phpdoc.entities;
            },
            entity(){
                return this.file.entity;
            },
            style(){
                let styles = {};
                if ( this.height ) {
                    styles['height'] = this.height + 'px';
                }
                return styles;
            },
            $zones(){
                return this.$$el.find('.drag-zone > .item');
            }
        },
        watch   : {
            query(val){
                this.open(val)
            }
        },
        methods : {
            open(query){
                this.query = query;
                this.$events.$emit('phpdoc:open', query);
                let parsed = this.$phpdoc.parseRef(query);
                return this.$phpdoc.getEntity(parsed.entityName).then(file => {
                    this.file = file;
                    let method = false;
                    if ( parsed.isMethod ) {
                        method = parsed.methodName;
                    } else if ( file.entity.methods.length ) {
                        method = file.entity.methods[0].name;
                    }
                    this.setMethod(method);
                    window.history.pushState(null, query, window.location.pathname + "#!/" + query);
                })
            },
            setMethod(methodName){
                this.methodName = methodName;
                if ( methodName === false ) {
                    return;
                }
                methodName = S(methodName).ensureRight('()')
                this.query = `${this.entity.full_name}::${methodName}`
            },
            onPropertySelect(propertyName){
                this.selectedProperty = propertyName;
            },
            onMethodSelect(methodName){
                this.selectedMethod = methodName;
                this.setMethod(methodName)
            },
            setActive(name){
                this.active = name;
                this.content = name === 'source' ? name : 'method';
                this.$emit('open', name);
                this.$root.$emit('resize')
            },
            isActive(name){
                return this.active === name;
            },

            onTreeSelect(query){
                this.open(query);
            },
            saveZoneWidths(){
                let zones = [],
                    self  = this;
                this.$$el.find('.drag-zone > .item').each(function (i) {
                    let zoneWidth = $(this).css('width');
                    if ( self.staticZoneWidths[i] !== undefined && self.staticZoneWidths[i] !== null ) {
                        zoneWidth = self.staticZoneWidths[i] + 'px';
                    }
                    zones.push(zoneWidth);
                })
                this.$cookie.set('phpdoc.zoneWidths', zones.join('-'))
                this.$events.$emit('phpdoc:zoneWidths', zones.join('-'))
            },
            loadZoneWidths(){
                let zoneWidths = this.$cookie.get('phpdoc.zoneWidths');
                if ( ! zoneWidths ) return;

                if ( ! zoneWidths.toString().length ) return;
                let zones = zoneWidths.split('-')
                this.$$el.find('.drag-zone > .item').each(function (i) {
                    $(this).css('width', zones[i]);
                });
                this.fixZoneWidths();
            },
            fixZoneWidths(){
                let totalZonesWidth = 0;
                this.$zones.each(function (i) {
                    totalZonesWidth+= $(this).width();
                })
                let totalWidth = this.$$el.width();
                let differenceWidth = totalWidth - totalZonesWidth;
                let $last = this.$zones.last();
                $last.css('width', this.$zones.last().width() + differenceWidth);
                console.log({totalWidth, totalZonesWidth})
            },
            showSource(){

            }
        }

    }
</script>




